
.card {
  width: 200px;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  padding: 32px 16px 16px;
  border-radius: 5px;
  background-color: var(--white-color);

  &__img {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto 40;
  }

  &__name {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: var(--main-color);
    margin: 0 0 4;
  }

  &__product {
    &-code {
      font-size: 10px;
      line-height: 14px;
      color: var(--secondary-color);
    }
  }

  &__review {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 24px;

    &-rating {
      color: var(--main-color);
    }
  }

  &__price {
    display: flex;
    margin-bottom: 16px;
    justify-content: space-between;

    &-label {
      font-size: 12px;
      line-height: 18px;
      font-weight: 400;
      color: var(--secondary-color);
    }

    &-sum {
      font-size: 16px;
      line-height: 18px;
      font-weight: 700;
      color: var(--main-color);
    }
  }

  &__buttom {
    display: flex;
    justify-content: center;
    height: 40px;
    align-items: center;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    color: var(--white-color);
    text-transform: uppercase;
    text-decoration: none;
    border-radius: 5px;
    background-color: var(--blue-color);
    box-sizing: border-box;
    padding: auto;

    &:hover {
      border: 1px solid var(--blue-color);
      background-color: var(--white-color);

      color: var(--blue-color);
    }
  }
}
