.card {
  box-sizing: border-box;
  background-color: var(--white-color);
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  color: var(--main-color);
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: var(--secondary-color);
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.card__review-rating {
  color: var(--main-color);
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: var(--secondary-color);
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-sum {
  color: var(--main-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buttom {
  color: var(--white-color);
  text-transform: uppercase;
  background-color: var(--blue-color);
  box-sizing: border-box;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  padding: auto;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buttom:hover {
  border: 1px solid var(--blue-color);
  background-color: var(--white-color);
  color: var(--blue-color);
}

.stars {
  display: flex;
}

.stars__stars {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  --main-color: #060b35;
  --secondary-color: #616070;
  --white-color: #fff;
  --blue-color: #00acdc;
}

html {
  color: var(--secondary-color);
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

body {
  margin: 0;
}
